import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import firebase from './firebase';
import UsersList from './UsersList';
import { BrowserRouter as Router, Link, Route, Switch } from 'react-router-dom';
const Landing = () => {
  //
  const [name, setName] = useState();
  const [surname, setSurname] = useState();
  const [location, setLocation] = useState();
  const [age, setAge] = useState();
  const [users, setUsers] = useState([]);
  const db = firebase.firestore();
  //

  const getName = (e) => {
    setName(e.target.value);
  }
  const getSurname = (e) => {
    setSurname(e.target.value);
  }
  const getLocation = (e) => {
    setLocation(e.target.value);
  }
  const getAge = (e) => {
    setAge(e.target.value);
  }
  const createUser = (e) => {
    e.preventDefault();
    db.collection('users').add({
      name: name,
      surname: surname,
      location: location,
      age: age
    })
      .then((res) => { console.log('user add') })
      .catch((err) => { console.log(err) })
  }

  useEffect(() => {
    let userinfo = [];
    db.collection('users').get()
      .then((res) => {

        res.forEach(action => {
          userinfo.push({ ...action.data(), id: action.id });
          console.log(action.data())
        })
        setUsers(userinfo);
      })

  }, [])
  const deleteUser = (e) => {
    let uid = e.target.id

    db.collection('users').doc(uid).delete()
      .then(() => { console.log("user deleted") })
      .catch((err) => { console.log(err) })
  }
  const updateUser = (e) => {
    let uid = e.target.
      db.collection('users').doc(uid).update({
        name: name,
        surname: surname,
        location: location,
        age: age

      })
      .then(() => { console.log("user update") })
      .catch((err) => { console.log(err) }
      )
  }

  return (
    <>
      
        <div className="banner-area">
          <h3></h3>
          <h3>FILL THE FORM</h3>
          <h3>FORM</h3>
          <form onSubmit={createUser}>

            <div className="input-group mt-3">


              <span className="input-group-text">Name</span>
              <input name="name" onChange={getName} type="text" aria-label="First name" className="form-control"></input>
              <span className="input-group-text">Surname</span>
              <input name="surname" onChange={getSurname} type="text" aria-label="Last name" className="form-control"></input>
              <span className="input-group-text">Location</span>
              <input name="location" onChange={getLocation} type="text" aria-label="Last name" className="form-control"></input>
              <span className="input-group-text">Age</span>
              <input name="age" onChange={getAge} type="number" aria-label="Last name" className="form-control"></input>
              <button type="submit" className="btn btn-primary "> Add User</button>

            </div>
          </form>
          
        </div>
      

    </>
  )
}
export default Landing;