import React, { Component, useState, useEffect } from 'react';
import UsersList from './UsersList';
import { BrowserRouter as Router, Link, Route, Switch } from 'react-router-dom';
import Details from './Details';
import UpDate from './UpDate';
import Landing from './Landing'
import 'bootstrap/dist/css/bootstrap.min.css';
import firebase from './firebase';
const Menu = () => {
    const [users, setUsers] = useState([]);
    const db = firebase.firestore();

    useEffect(() => {
        let userinfo = [];
        db.collection('users').get()
            .then((res) => {

                res.forEach(action => {
                    userinfo.push({ ...action.data(), id: action.id });
                    console.log(action.data())
                })
                setUsers(userinfo);
            })

    }, [])

    const deleteUser = (e) => {
        let uid = e.target.id

        db.collection('users').doc(uid).delete()
            .then(() => { console.log("user deleted") })
            .catch((err) => { console.log(err) })
    }

       

    return (

        <>
            <Router>

                <div className="box-area">
                    <header>

                        <div className="logo">
                            <h1>LOVERS DA LUK</h1>
                        </div>
                        <nav className="navigator">
                            <ul>
                                <li>
                                    <div class="btn-group" role="group" aria-label="Third group">
                                        <button type="button" class="btn btn-info"><Link to="/Landing">ADD USER</Link></button>
                                    </div>

                                </li>
                                

                                <li>
                                <div class="btn-group" role="group" aria-label="Third group">
                                        <button type="button" class="btn btn-info"><Link to="/Details">DETAILS</Link></button>
                                    </div>
                                    
                                </li>
                            </ul>
                        </nav>

                    </header>
                
                <Switch>
                    <Route
                        path="/Details">
                        <Details/>
                    </Route>

                    <Route
                        path={"/Landing"}>
                        <Landing/>
                    </Route>
                </Switch>
                </div>
            </Router>
            {users.map(action =>
                <div className="card">
                    <div className="card mt-3" key={action.id} >
                        <div className="card-body">
                            <h6 className="card-title">User Name</h6>
                            <p className="card-text">{action.name}</p>
                            <button  type="button" class="btn btn-success">EDIT</button>
                            <button  id={action.id} onClick={deleteUser} type="button" class="btn btn-danger">DELETE</button>
                            <button type="button" class="btn btn-warning">VIEW</button>

                        </div>
                    </div>

                </div>
            )}            
            
        </>
    )
}

export default Menu;